var timer = null;
import {
	myRequestPost
} from '@/utils/request.js';
import uniIcons from "@/components/uni/uni-icons/uni-icons.vue"
export default {
	data() {
		return {
			menu: []

		};
	},
	components: {
		uniIcons
	},
	onLoad() {
		this.getMenu()
	},
	methods: {
		linktolodetail() {
			uni.navigateTo({
				url: "/pages/lotteryDetail/lotteryDetail"
			})
		},
		async getMenu() {
			let result = await myRequestPost("/rest/cks/api/recommender/user/get", {
				"userId": null,
				"pageNo": 0,
				"pageSize": 25
			})
			console.log(result.cookbooks);
			this.menu = result.cookbooks.slice(1, 7);
		},
		///api/cookbook/by-name/get
		linktomenu(item) {
			uni.navigateTo({
				url: "/pages/fooddetail/fooddetail?id=" + item.id
			})
		},
		linktolosearch() {
			uni.navigateTo({
				url: "/pages/search/search"
			})
		}
	}

}
